import React, { useState } from "react";
import Chart from "./components/Chart";
import Population from "./components/Population";
import "./App.css";
import CurveGraph from "./components/Sales";
import Chord from "./components/Chord";
import PieGraph from "./components/PieGraph";
import TreeMap from "./components/TreeMap";
import SunBurst from "./components/SunBurst";
import Timeline from "./components/Timeline";
import Topology from "./components/Topology";

function App(props) {
  const [show, setShow] = useState({
    population:false,
    curve:false,
    chord:false,
    Pie:false,
    Tree:false,
    sunburst:false,
    timeline:false,
    topology:false,
  });

  const showGraph = (e) => {
    e.preventDefault();
    setShow({...show, population:!show.population});
  };
  return (
    <div className="app">
      <div className="app__header">
        <h3 className="header__logo">Admin</h3>
      </div>
      <div className="app_button">
        <button onClick={showGraph}>{show.population?("Hide the graph"):("Show The Graph")}</button>
      </div>
      {show.population?(  <Population />):null}
      {/* <Chart /> */}

      <div className="app_button">
        <button onClick={(e)=>{
          e.preventDefault()
          setShow({...show,curve:!show.curve}) 
          }}>{show.curve?("Hide the graph"):("SALES GRAPH")}</button>
      </div>

      {show.curve?(<CurveGraph />):null}
      <div className="app_button">
        <button onClick={(e)=>{
          e.preventDefault()
          setShow({...show,chord:!show.chord}) 
          }}>{show.chord?("Hide the graph"):("CHORD GRAPH")}</button>
      </div>

      {show.chord?( <Chord />):null}
      <div className="app_button">
        <button onClick={(e)=>{
          e.preventDefault()
          setShow({...show,pie:!show.pie}) 
          }}>{show.pie?("how The Graph"):("PIE GRAPH")}</button>
      </div>

      {show.pie?(<PieGraph />):null}

      <div className="app_button">
        <button onClick={(e)=>{
          e.preventDefault()
          setShow({...show,tree:!show.tree}) 
          }}>{show.tree?("Hide the graph"):("TREE GRAPH")}</button>
      </div>

      {show.tree?(<TreeMap/>):null}

      <div className="app_button">
        <button onClick={(e)=>{
          e.preventDefault()
          setShow({...show,sunburst:!show.sunburst}) 
          }}>{show.sunburst?("Hide the graph"):("SUNBURST GRAPH")}</button>
      </div>

      {show.sunburst?(<SunBurst/>):null}
      
      <div className="app_button">
        <button onClick={(e)=>{
          e.preventDefault()
          setShow({...show,timeline:!show.timeline}) 
          }}>{show.timeline?("Hide the graph"):("TIMELINE")}</button>
      </div>

      {show.timeline?( <Timeline />):null}

      <div className="app_button">
        <button onClick={(e)=>{
          e.preventDefault()
          setShow({...show,topology:!show.topology}) 
          }}>{show.topology?("Hide the graph"):("Force Directed Graph")}</button>
      </div>

      {show.topology?( <Topology/>):null}
      
      
     
    
    </div>
  );
}
export default App;
